
.mv-section{width:100%;height:100vh;margin-bottom:1px;opacity:1
}
.mv-section:nth-child(odd){background-color:#ccc
}
.lds-ring{display:inline-block;position:relative;width:80px;height:80px
}
.lds-ring div{-webkit-box-sizing:border-box;box-sizing:border-box;display:block;position:absolute;width:64px;height:64px;margin:8px;border:8px solid #666;border-radius:50%;-webkit-animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:#666 transparent transparent transparent
}
.lds-ring div:nth-child(1){-webkit-animation-delay:-0.45s;animation-delay:-0.45s
}
.lds-ring div:nth-child(2){-webkit-animation-delay:-0.3s;animation-delay:-0.3s
}
.lds-ring div:nth-child(3){-webkit-animation-delay:-0.15s;animation-delay:-0.15s
}
@-webkit-keyframes lds-ring{
0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)
}
100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)
}
}
@keyframes lds-ring{
0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)
}
100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)
}
}

h1{opacity:0;-webkit-transition:opacity 0.5s ease, -webkit-transform 1s ease;transition:opacity 0.5s ease, -webkit-transform 1s ease;transition:opacity 0.5s ease, transform 1s ease;transition:opacity 0.5s ease, transform 1s ease, -webkit-transform 1s ease;-webkit-transform:translateX(-300px);transform:translateX(-300px)
}
h1.active{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)
}

